@import "../mixins/mixins";

.discount__block {
    width: 100wh;
    height: 740px;
    margin-bottom: 80px;

    .action__box {
        display: flex;
        justify-content: space-between;

        .wrap {
            width: 660px;
            height: 674px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            &:first-child {
                margin-right: 30px;
            }

            .title {
                max-width: 460px;
                margin-bottom: 20px;

                &__text {
                    font-weight: normal;
                    font-size: 72px;
                    line-height: 125%;
                    margin-bottom: 10px;
                }

                &__description {
                    width: 100%;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 18px;
                    color: #888888;
                }
            }

            .discount-first {
                text-decoration: none;
                color: #333333;
                display: flex;
                justify-content: flex-end;
                position: relative;
                width: 100%;
                height: 460px;
                background: linear-gradient(129.35deg, #cad8cd 9.78%, #8e9b7f 87.39%);
                @include discount("../img/tent.png");

                &:hover .title_buy::before {
                    width: 100%;
                    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
                }
            }

            .discount-second {
                text-decoration: none;
                color: #333333;
                display: flex;
                justify-content: flex-end;
                position: relative;
                width: 100%;
                height: 400px;
                background: linear-gradient(110.17deg, #a7d4f4 0%, #77b4e0 100%);
                @include discount("../img/blue-tent.png");

                &:hover .title_buy::before {
                    width: 100%;
                    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
                }
            }

            .email {
                width: 100%;
                height: 244px;
                background: #e0e0e0;
                padding: 30px;

                &__title {
                    margin-bottom: 40px;

                    &_main {
                        font-weight: normal;
                        font-size: 48px;
                        line-height: 54px;
                        margin-bottom: 10px;
                    }

                    &_notification {
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 18px;
                        color: #888;
                    }
                }

                &__send {
                    max-width: 495px;
                    height: 50px;
                    display: flex;
                    justify-content: space-between;

                    .input_email {
                        width: 60%;
                        border: 1px solid #888888;
                        outline: none;
                        background-color: #e0e0e0;
                        padding-left: 20px;
                        margin-right: 20px;

                        &::placeholder {
                            color: #888888;
                        }
                    }

                }
            }
        }
    }


    .link__center {
        width: 103px;
        font-size: 18px;
        line-height: 20px;
        display: block;
        margin: 0 auto;
    }

    @import '../class/class';
}

@import "./media/discount-media";